    link: "https://www.meetup.com/German-PowerShell-Usergroup/events/273957765/",
    description: "<p>#region München MeetUp<br/><a href=\"https://github.com/GPSUG/Munich\" class=\"linkified\">https://github.com/GPSUG/Munich</a></p> <p>-- English below</p> <p>Hi Zusammen,</p> <p>wir möchten trotz #WFH (work from home) den Community-Gedanken am Leben erhalten und liefern unser PowerShell Meetup virtuell.</p> <p>Das virtuelle Event soll möglichst interaktiv und dynamisch gestaltet werden, bringt also eure Fragen und Anregungen gerne mit.</p> <p>Den Meeting link findet ihr kurz vor Start hier auf der Seite.</p> <p>Joint uns gern auf Slack um in Kommunikation zu bleiben! <a href=\"https://github.com/GPSUG/Munich#slack\" class=\"linkified\">https://github.com/GPSUG/Munich#slack</a><br/>____________________________________________________________</p> <p>$Start = \"19:00\" (7PM)<br/>____________________________________________________________</p> <p>The Sessions:</p> <p>############</p> <p>19:00 - 20:00 Powershell by Jason Helmick - PowerShell Product Group (@theJasonHelmick)</p> <p>Jason wil provide a topic and afterwards you can ask him questions directly.</p> <p>20:00 - 20:45 Free Slot</p> <p>Anyone who wants, can submit a session.<br/>______________________________________________________</p> <p>Hi,</p> <p>even during WFH (work from home) we want to keep the community spirit alive by hosting the next PowerShell meetup online.</p> <p>This virtual event is supposed to be open and interactive. Feel free to bring your questions and topics into the discussion.</p> <p>The meeting link will be posted shortly before the event on this site.</p> <p>For all updates look into the Meetup event.</p> <p>Please also consider joining us on Slack to stay in contact before and after the meetup <a href=\"https://github.com/GPSUG/Munich#slack\" class=\"linkified\">https://github.com/GPSUG/Munich#slack</a></p> ",
