    description: "<p>A prize-winning bestseller in its native France, a vivid and evocative coming-of-age tale, set against the backdrop of the Rwandan genocide and the civil war in Burundi, of a young boy's childhood innocence shattered by the brutal tides of history.</p> <p>In 1992, Gabriel, ten years old, lives in Burundi in a comfortable expatriate neighborhood with his French father, his Rwandan mother and his little sister, Ana. In this joyful idyll, Gabriel spends the better part of his time with his mischievous band of friends, in a tiny cul-de-sac they have turned into their kingdom. But their peaceful existence will suddenly shatter when this small African country is brutally battered by history.<br/>In this magnificent coming-of-age story, Gael Faye describes an end of innocence and drives deep into the heart and mind of a young child caught in the maelstrom of history. [Source: goodreads]</p> ",
